* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  outline: none;
}

body {
  font-family: Arial, Helvetica, sans-serif;
  line-height: 1.4;
  width: 50%;
  margin: 20px auto;
}

a {
  color: #333;
  text-decoration: none;
}

.btn-close {
  background: lightcoral;
  color: #fff;
  border: none;
  padding: 5px 9px;
  border-radius: 50%;
  cursor: pointer;
  float: right;
  transition: .3s;
}

.header {
  background: #333;
  color: #fff;
  text-align: center;
  padding: 10px;
}

.container {
  padding: 0 1rem;
}

.btn-submit {
  display: inline-block;
  border: none;
  background: #555;
  color: #fff;
  padding: 7px 20px;
  cursor: pointer;
  flex: 1;
}

.btn-submit:hover {
  background: #666;
}

form {
  display: flex;
}

form input {
  flex: 10;
  padding: 5px;
}

.btn-close:hover {
  background: red;
}

@media screen and (max-width:375px){
  body{
    width: 100%;
  }
}

@media screen and (max-width:450px){
  body{
    width: 100%;
  }
}

@media screen and (max-width:1024px){
  body{
    width: 70%;
  }
}